import BaseDropdown from '@/components/filters/BaseDropdown'
import BaseSearch from '@/components/filters/BaseSearch'
import BaseDateTimeRange from '@/components/filters/BaseDateTimeRange'
export default {
  components: {
    BaseDropdown,
    BaseSearch,
    BaseDateTimeRange
  },

  props: {
    redirect: {
      type: Boolean,
      required: false,
      default: false
    },
    dates: {
      type: Boolean,
      required: false,
      default: true
    },
    options: {
      type: Boolean,
      required: false,
      default: true
    },
    search: {
      type: Boolean,
      required: false,
      default: true
    },
    section: {
      type: String,
      required: false,
      default: 'section'
    },
    sectionText: {
      type: String,
      required: false,
      default: 'section text'
    }
  },

  data () {
    return {
      baseUrl: process.env.API_URL_SERVER
    }
  }
}
